@font-face {
  font-family: "roboto";
  src: url(./fonts/roboto/roboto-regular.woff2) format("woff2"),
    url(./fonts/roboto/roboto-regular.woff) format("woff"),
    url(./fonts/roboto/roboto-regular.ttf) format("ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "notosans";
  src: url(./fonts/noto_sans/notosans-regular.woff2) format("woff2"),
    url(./fonts/noto_sans/notosans-regular.woff) format("woff"),
    url(./fonts/noto_sans/notosans-regular.ttf) format("ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "objective";
  src: url(./fonts/objective/objective-regular.woff2) format("woff2"),
    url(./fonts/objective/objective-regular.woff) format("woff"),
    url(./fonts/objective/objective-regular.otf) format("otf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "objective";
  src: url(./fonts/objective/objective-medium.woff2) format("woff2"),
    url(./fonts/objective/objective-medium.woff) format("woff"),
    url(./fonts/objective/objective-medium.otf) format("otf");
  font-weight: 500;
  font-style: normal;
}

body {
  margin: 0;
  font-family: "notosans", "objective", "Roboto", sans-serif;
}

